import { Component, OnInit, Inject  } from '@angular/core';
import { MdDialogRef, MD_DIALOG_DATA, MdSnackBar } from '@angular/material';
import { FormGroup, FormBuilder } from '@angular/forms';
import { LoaderService, HelperService } from '../../../shared';
import { Response } from '@angular/http';
import { isNumber, isNullOrUndefined } from 'util';
import { BaseList } from '../../bases/base-list';

@Component({
    template: require('./modal-pharmaceutical.component.html')
})

export class ModalPharmaceuticalComponent extends BaseList  implements OnInit {

    private model: any = {};
    private action_active: boolean = false;
    private str_action: string = 'Guardar';
    private pharmaceutical_form: any[] = [];
    private routes_administration: any[] = [];
    private storage_condition: any[] = [];
    private option: any = 1;
    radio = {
        option : '1',
      };

    constructor(
      private formBuilder: FormBuilder,
      private dialogRef: MdDialogRef<ModalPharmaceuticalComponent>,
      @Inject(MD_DIALOG_DATA) private data,
      public loaderService: LoaderService,
      public helperService: HelperService,
      public snackBar: MdSnackBar,
    ) {
        super(loaderService, helperService);
        this.urlApi = '/api/pharmaceuticaldrug';
    }

    ngOnInit() {
        this.model = {};
        this.clean();
        this.getCollection();
    }

    /**
     * get the country and the tax regime with the collection of names
     */
    private getCollection(){
        this.loaderService.display(true);
        this.helperService.POST(`/api/collections`, ['PHARMACEUTICAL_FORM', 'ROUTES_ADMINISTRATION', 'STORAGE_CONDITION'])
        .map((response: Response) => {

            const res = response.json();
            this.pharmaceutical_form = res.PHARMACEUTICAL_FORM;
            this.routes_administration = res.ROUTES_ADMINISTRATION;
            this.storage_condition = res.STORAGE_CONDITION;

        }).subscribe(
            (error) => {
                this.loaderService.display(false);
            },
            (done) => {
                this.loaderService.display(false);
            });
    }

    private save() {

        /** Create */
        this.loaderService.display(true);
        this.helperService.POST(`/api/pharmaceuticaldrug`, this.model)
        .map((response: Response) => {

            const res = response.json();
            if (res.status === 'success') {
                this.snackBar.open(res.message, 'Guardado', {
                    duration: 3500,
                });
                this.dialogRef.close(res.data);
                this.clean();
            }

        }).subscribe(
            (error) => {
                this.loaderService.display(false);
            },
            (done) => {
                this.loaderService.display(false);
            });

    }

    private clean() {
        this.model = {};
        this.model.is_pos = false;
        this.model.is_controlled = false;
        this.model.is_monopoly = false;
        this.model.state = true;
    }

    add() {
      this.dialogRef.close(this.model);
    }

    private getDepartments(){
        this.loaderService.display(true);
        this.helperService.GET(`/api/departamentos?pais_id=${this.model.country_id}`)
        .map((response: Response) => {

            const res = response.json();

        }).subscribe(
            (error) => {
                this.loaderService.display(false);
            }, (done) => {
                this.loaderService.display(false);
            });
    }

    private addFromTable(obj) {
        this.loaderService.display(true);
        this.helperService.GET(`/api/pharmaceuticaldrug/${obj.id}`)
        .map((response: Response) => {

            const res = response.json();
            this.dialogRef.close(res.data);

        }).subscribe(
            (error) => {
                this.loaderService.display(false);
            }, (done) => {
                this.loaderService.display(false);
            });
    }

  }



